<h1 id="android-notifications" tabindex="-1"><a class="header-anchor" href="#android-notifications" aria-hidden="true">#</a> Android Notifications</h1>
<p>Services have no graphical user interface, so what if you have code running in the background that suddenly needs to inform the user about something? Notifications to the rescue. Use them to get your users' attention, but try avoid annoying the user too much.</p>
